import React, { useEffect, useRef } from "react"
import Layout from "../components/layout"
import { graphql } from "gatsby"
import NewsletterBox from "../components/newsletterBox"
import SEO from "../components/seo"
import Button from "react-bulma-components/lib/components/button"
import Container from "react-bulma-components/lib/components/container"
import Section from "react-bulma-components/lib/components/section"
import NewsletterSubscribe from "../components/newsletterSubscribe"
import Navbar from "../components/navbar"

export default ({ data }) => {
  const titles = data.allNewJson.edges[0].node.newsletter.parts.map(data => {
    return data.title
  }, [])

  const sectionRef = useRef()

  useEffect(() => {
    sectionRef.current.scrollIntoView({
      behavior: "smooth",
      block: "nearest",
      inline: "center",
    })
  })

  return (
    <Layout>
      <SEO
        title={data.allNewJson.edges[0].node.title}
        keywords={[
          `ecotainment`,
          `newsletter`,
          `archiv`,
          data.allNewJson.edges[0].node.title,
        ]}
        description={`${
          data.allNewJson.edges[0].node.title
        }: featuring: ${titles.join(", ")}`}
      />
      <Container>
        <Navbar />
        <NewsletterSubscribe />
        {data.allNewJson.edges.map(({ node }, index) => (
          <div key={index} ref={sectionRef}>
            <Section>
              <NewsletterBox node={node} />
              <Button
                onClick={() => {
                  typeof window.history !== "undefined" && window.history.back()
                }}
                color={"primary"}
                fullwidth={true}
              >
                Zurück
              </Button>
            </Section>
          </div>
        ))}
      </Container>
    </Layout>
  )
}

export const query = graphql`
  query($slug: String!) {
    allNewJson(filter: { slug: { eq: $slug } }) {
      edges {
        node {
          published
          title
          tags
          newsletter {
            welcome
            intro
            parts {
              emoji {
                label
                icon
              }
              title
              tags
              text
            }
            outro
          }
        }
      }
    }
  }
`
